
export const APICOMMANDS = {
    filter: filter,
    getIDs: getIDs,
    getItems: getItems,
    getFields: getFields
}
//----- –§—É–Ω–∫—Ü–∏–∏ –∑–∞–ø—Ä–æ—Å–æ–≤ –∫ API ---------------------------------------------------
function filter(params: {}) {
    /*  –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —É–ø–æ—Ä—è–¥–æ—á–µ–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä–æ–≤ —Ç–æ–≤–∞—Ä–æ–≤,
        —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏—Ö –∑–∞–¥–∞–Ω–Ω–æ–º—É –∑–Ω–∞—á–µ–Ω–∏—é.

        –í –∫–∞—á–µ—Å—Ç–≤–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ –º–æ–∂–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –ª—é–±–æ–µ –ø–æ–ª–µ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º–æ–µ 
        –º–µ—Ç–æ–¥–æ–º get_fields –±–µ–∑ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤. –í –∫–∞—á–µ—Å—Ç–≤–µ –∑–Ω–∞—á–µ–Ω–∏—è –¥–æ–ª–∂–µ–Ω 
        –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è —Ç–∏–ø –¥–∞–Ω–Ω—ã—Ö —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π –ø–æ–ª—é. –î–ª—è –ø–æ–ª—è product 
        –±—É–¥–µ—Ç –ø—Ä–æ–≤–µ—Ä—è—Ç—å—Å—è –≤—Ö–æ–∂–¥–µ–Ω–∏–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ –≤ —Å—Ç—Ä–æ–∫—É. 
        –î–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö –ø–æ–ª–µ–π –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è —Å—Ç—Ä–æ–≥–æ–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏–µ.
    */
    return {
        "action": "filter",
        "params": params
    }
}
function getIDs(params: { offset: number, limit: number }) {
    /*  –º–µ—Ç–æ–¥ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —É–ø–æ—Ä—è–¥–æ—á–µ–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä–æ–≤ —Ç–æ–≤–∞—Ä–æ–≤
        <offset: number> —Å–º–µ—â–µ–Ω–∏–µ –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ –Ω–∞—á–∞–ª–∞ —Å–ø–∏—Å–∫–∞
        <limit: number> –∂–µ–ª–∞–µ–º–æ–µ —á–∏—Å–ª–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã—Ö –∑–∞–ø–∏—Å–µ–π
    */
    return {
        "action": "get_ids",
        "params": params
    }
}
function getItems(params: { ids: Array<string> }) {
    /*  –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —É–ø–æ—Ä—è–¥–æ—á–µ–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ —Ç–æ–≤–∞—Ä–æ–≤ —Å–æ –≤—Å–µ–º–∏ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∞–º–∏,
        –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω—ã –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä—ã —Ç–æ–≤–∞—Ä–æ–≤.
        <ids:Array<string>> –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä—ã —Ç–æ–≤–∞—Ä–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥—É—Ç –≤–æ–∑–≤—Ä–∞—â–µ–Ω—ã.
    */
    return {
        "action": "get_items",
        "params": params
    }
}
function getFields(params: { field: string, offset: number, limit: number }) {
    /*  –±–µ–∑ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —É–ø–æ—Ä—è–¥–æ—á–µ–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ –∏–º–µ—é—â–∏—Ö—Å—è –ø–æ–ª–µ–π —Ç–æ–≤–∞—Ä–æ–≤
        <field: string> –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–æ–ª—è —Ç–æ–≤–∞—Ä–∞
        <offset: number> —Å–º–µ—â–µ–Ω–∏–µ –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ –Ω–∞—á–∞–ª–∞ —Å–ø–∏—Å–∫–∞
        <limit: number> –∂–µ–ª–∞–µ–º–æ–µ —á–∏—Å–ª–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã—Ö –∑–∞–ø–∏—Å–µ–π
    */
    return {
        "action": "get_fields",
        "params": params
    }
}

